/* reset css */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.5rem;
  text-transform: uppercase;
  color: white;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Custom Additions Below */
:root, * {
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
  color: white;
}

body {
  width: 100%;
  height: 100vh;
  /* header styling */
  /* showcase styling */
}
body header {
  position: absolute;
  min-height: 100vh;
}
body header .header__container {
  padding: 3.2rem 2.5rem 0 2.5rem;
}
body header .header__container nav.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 33rem;
}
body header .header__container nav.header__nav img {
  display: block;
  width: 13.5rem;
  height: 2.5rem;
}
body header .header__container nav.header__nav button#times {
  display: none;
}
body header .header__container nav.header__nav ul.nav__nav-list {
  display: flex;
  flex-direction: column;
  gap: 2.1rem;
  width: 100%;
  margin-top: 16rem;
  font-size: 2.5rem;
  text-transform: capitalize;
  opacity: 0.7;
  display: none;
}
body header .header__container nav.header__nav ul.nav__nav-list.show {
  display: flex;
}
body section.showcase {
  padding: 22rem 2rem 0 2rem;
  min-height: 100vh;
  background-image: url(/images/mobile/image-hero.jpg);
  background-repeat: no-repeat;
  background-size: contain;
}
body section.showcase .showcase__content {
  padding: 2.3rem 3rem 1.7rem 2.5rem;
  font-size: 2.6rem;
  letter-spacing: 0.05rem;
  border: 0.1995rem solid white;
}

@media screen and (min-width: 1440px) {
  body {
    /* header styling */
    /* showcase styling */
  }
  body header {
    min-width: 100vw;
  }
  body header .header__container {
    padding: 5.5rem 16.5rem 0rem 16.5rem;
  }
  body header .header__container nav.header__nav {
    flex-wrap: nowrap;
  }
  body header .header__container nav.header__nav .nav__logo {
    flex: 1;
  }
  body header .header__container nav.header__nav .nav__logo img {
    width: 100%;
    min-width: 19.4rem;
    min-height: 3.2rem;
  }
  body header .header__container nav.header__nav button {
    display: none;
  }
  body header .header__container nav.header__nav ul.nav__nav-list {
    display: flex;
    flex-direction: row;
    margin: 0 0 0 56rem;
    font-size: 1.7rem;
    opacity: 1;
  }
  body header .header__container nav.header__nav ul.nav__nav-list li:hover::after {
    content: "";
    display: block;
    width: 40%;
    border-bottom: 0.2rem solid white;
    margin: 0.6rem 0 0 1.5rem;
  }
  body section.showcase {
    background-image: url(/images/desktop/image-hero.jpg);
    padding: 22rem 2rem 0 16.5rem;
  }
  body section.showcase .showcase__content {
    margin-right: 60rem;
    padding: 4.3rem 3rem 1.7rem 2.5rem;
    font-size: 5rem;
  }
}